body {
	font-size: 16px;
	font-family: 'Heeboo', sans-serif;
}

p {
	text-shadow: -2px 10px 11px #3d3020;
}

.hero {
	background-image: url(/dist/img/energy-poverty_opt.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position-x: 75%;
	display: flex;
	color: white;
	font-family: 'Heebo', sans-serif;
	font-size: 1.2rem;
	height: 100vh;
	width: 100%;
}

.hero.mobile {
	justify-content: space-between;
	flex-direction: column;
}

@media screen and (min-width: 768px) {
	.hero.mobile {
		display: none;
	}
}

.hero.mobile .psudo-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.hero.mobile .psudo-header h1 {
	margin-left: 5%;
}

.hero.mobile .psudo-header .hamburger {
	width: 50px;
	height: 50px;
	position: relative;
	background-color: rgba(0, 0, 0, 0);
	border: 5px solid rgba(0, 0, 0, 0);
	cursor: pointer;
	transition: all 0.3s ease;
	margin-right: 5%;
	margin-top: 2%;
	z-index: 9999;
}

@media screen and (min-width: 768px) {
	.hero.mobile .psudo-header .hamburger {
		display: none;
	}
}

.hero.mobile .psudo-header .hamburger div {
	height: 2px;
	width: 30px;
	background-color: white;
	position: absolute;
	transition: all 0.3s ease;
}

.hero.mobile .psudo-header .hamburger div.hamburgerLineOne {
	top: 7px;
	left: 5px;
}

.hero.mobile .psudo-header .hamburger div.hamburgerLineTwo {
	top: 17px;
	left: 5px;
}

.hero.mobile .psudo-header .hamburger div.hamburgerLineThree {
	top: 27px;
	left: 5px;
}

.hero.mobile .psudo-header .hamburger:hover {
	border-radius: 50%;
	transition: all 0.3s ease;
}

.hero.mobile .psudo-header .hamburger:hover .hamburgerLineOne {
	transform: translateY(10px) rotate(45deg);
	transition: all 0.5s ease;
}

.hero.mobile .psudo-header .hamburger:hover .hamburgerLineTwo {
	opacity: 0;
	transition: all 0.2s ease;
}

.hero.mobile .psudo-header .hamburger:hover .hamburgerLineThree {
	transform: translateY(-10px) rotate(-45deg);
	transition: all 0.5s ease;
}

.hero.mobile .psudo-header .hamburger.focus {
	border-radius: 50%;
	transition: all 0.3s ease;
}

.hero.mobile .psudo-header .hamburger.focus .hamburgerLineOne {
	transform: translateY(10px) rotate(45deg);
	transition: all 0.5s ease;
}

.hero.mobile .psudo-header .hamburger.focus .hamburgerLineTwo {
	opacity: 0;
	transition: all 0.2s ease;
}

.hero.mobile .psudo-header .hamburger.focus .hamburgerLineThree {
	transform: translateY(-10px) rotate(-45deg);
	transition: all 0.5s ease;
}

.hero.mobile .content {
	width: 100%;
	opacity: 0;
	height: 100vh;
	display: block;
	position: absolute;
	right: 100%;
	transition: 0.5s;
	display: flex;
	justify-content: center;
	align-items: center;
}

.hero.mobile .content .flex {
	display: flex;
	height: 50%;
	flex-direction: column;
	justify-content: center;
}

.hero.mobile .content .navigation {
	padding-left: 0px;
	text-align: center;
	font-size: 30px;
	font-weight: bold;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 100%;
}

.hero.mobile .content .navigation ul {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-content: center;
	height: 100%;
}

.hero.mobile .content .navigation ul a {
	color: white;
	text-decoration: none;
	position: relative;
	display: inline-block;
	overflow: hidden;
	transition: all 0.2s;
}

.hero.mobile .content .navigation ul a:before {
	content: '';
	overflow: hidden;
	width: 100%;
	position: absolute;
	left: -150%;
	bottom: 0px;
	height: 3px;
	background-color: red;
	transition: all 0.3s;
}

.hero.mobile .content .navigation ul a:hover {
	color: red;
}

.hero.mobile .content .navigation ul a:hover:before {
	left: 0px;
}

.hero.mobile .content.showing {
	right: 0px;
	opacity: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.5);
}

.hero.mobile #logo {
	width: 153px;
	margin: 5% auto;
}

